import request from '@/utils/request'
export function fetchList(params) {
  return request({
    url:'/order/list',
    method:'get',
    params:params
  })
}

export function closeOrder(params) {
  return request({
    url:'/order/update/close',
    method:'post',
    params:params
  })
}

export function deleteOrder(params) {
  return request({
    url:'/order/delete',
    method:'post',
    params:params
  })
}

export function deliveryOrder(data) {
  return request({
    url:'/order/update/delivery',
    method:'post',
    data:data
  });
}

export function getOrderDetail(id) {
  return request({
    url:'/order/'+id,
    method:'get'
  });
}

export function updateReceiverInfo(data) {
  return request({
    url:'/order/update/receiverInfo',
    method:'post',
    data:data
  });
}

export function updateMoneyInfo(data) {
  return request({
    url:'/order/update/moneyInfo',
    method:'post',
    data:data
  });
}

export function updateOrderNote(params) {
  return request({
    url:'/order/update/note',
    method:'post',
    params:params
  })
}

//new
export function addOrder(data) {
  let res = {
    code: 200,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  });

  return request({
    url:'/order/add',
    method:'post',
    data:data
  });
}

export function fetchOrderList(params) {
  let orderList = {
    code: 200,
    message: "success",
    data: {
      list: [{
        orderSn: "SN512231",
        customerName: "Tom",
        customerId: 1,
        totalAmount: 3505,
        orderDate: 1628472604623,
        orderOther: "此处为样例备注",
        goodList: [{
          firm: "喜马诺",
          stock: "飞轮",
          model: "HG200-9",
          price: 62,
          num: 5
        }, {
          firm: "Michelin",
          stock: "轮胎2",
          model: "215/70R15-2",
          price: 639,
          num: 5
        }]
      }],
      total: 11
    }
  };
  return new Promise(resolve => {
    resolve(orderList)
  })

  return request({
    url:'/order/list',
    method:'get',
    params:params
  });
}




