<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input v-model="listQuery.keyword" class="input-width" placeholder="角色名称" clearable></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
<!--      <el-button size="mini" class="btn-add" @click="handleAddCustomer()" style="margin-left: 20px">添加</el-button>-->
    </el-card>
    <div class="table-container">
      <el-table ref="roleTable"
                :data="list"
                style="width: 100%;"
                v-loading="listLoading" border>
        <el-table-column label="订单号" align="center">
          <template slot-scope="scope">{{scope.row.orderSn}}</template>
        </el-table-column>
        <el-table-column label="购买方" align="center">
          <template slot-scope="scope">{{scope.row.customerName}}</template>
        </el-table-column>
        <el-table-column label="日期" align="center">
          <template slot-scope="scope">{{scope.row.orderDate | formatDateTime}}</template>
        </el-table-column>
        <el-table-column label="合计" align="center">
          <template slot-scope="scope">￥{{scope.row.totalAmount }}</template>
        </el-table-column>
        <el-table-column label="备注" align="center">
          <template slot-scope="scope">{{scope.row.orderOther }}</template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleUpdateCustomer(scope.$index, scope.row)">查看详情
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination-container">
      <el-pagination

        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="编辑客户信息"
      :visible.sync="editInfo.dialogVisible"
      :top = "'10vh'"
      width="60%">
      <el-form :model="editInfo.customer" :rules="rules" ref="customerInfoForm"  label-width="15%" style="width: 90%;margin: auto" size="small">
        <el-form-item label="名字：" prop="customerName">
          <el-input v-model="editInfo.customer.customerName"></el-input>
        </el-form-item>
        <el-form-item label="联系方式：" prop="customerTel">
          <el-input v-model="editInfo.customer.customerTel"></el-input>
        </el-form-item>
        <el-form-item label="备注：" prop="other">
          <el-input v-model="editInfo.customer.other"
                    type="textarea"
                    :rows="5"></el-input>
        </el-form-item>
        <el-form-item style="text-align: center">
          <el-button type="primary" size="medium" @click="handleEditCustomer('customerInfoForm')">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
  import {formatDate} from '@/utils/date';
  import {delCustomer, fetchCustomerList, updateCustomer} from "@/api/customer";
  import {fetchOrderList} from "@/api/order";

  const defaultListQuery = {
    pageNum: 1,
    pageSize: 5,
    keyword: null
  };
  export default {
    name: 'orderList',
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        list: null,
        total: null,
        listLoading: false,
        dialogVisible: false,
        isEdit: false,
        editInfo:{
          dialogVisible:false,
          customer : {}
        },
        rules: {
          customerName:  [{required: true, message: '请输入', trigger: 'blur'}],
          customerTel: [{required: true, message: '请输入', trigger: 'blur'}],
          other: [{required: false, message: '请输入', trigger: 'blur'}],
        }
      }
    },
    created() {
      this.getOrderList();
    },
    filters: {
      formatDateTime(time) {
        if (time == null || time === '') {
          return 'N/A';
        }
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd')
      }
    },
    methods: {
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getOrderList();
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getOrderList();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getOrderList();
      },

      handleUpdateCustomer(index,row){
        return;
        this.$nextTick(()=>{
          if (this.$refs['customerInfoForm'] != null)
            this.$refs['customerInfoForm'].resetFields();
        })
        this.editInfo.customer = Object.assign({}, row);
        this.editInfo.dialogVisible=true;
      },
      handleDeleteCustomer(index, row){
        this.$confirm('是否要进行删除操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          delCustomer(row.customerId).then(response => {
            this.$message({
              message: '删除成功',
              type: 'success',
              duration: 1000
            });
            this.getCustomerList();
          });
        });
      },
      handleEditCustomer(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('是否要进行修改', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              updateCustomer(this.editInfo.customer.customerId, this.editInfo.customer).then(response => {
                this.$message({
                  message: '修改成功',
                  type: 'success',
                  duration: 1000
                });
                this.editInfo.dialogVisible = false;
                this.getCustomerList();
              });
            });
          }
        });
      },
      handleAddCustomer() {
        this.$router.push({path:'/customer/cCustomer'});
      },
      getOrderList() {
        this.listLoading = true;
        fetchOrderList(this.listQuery).then(response => {
          this.list = response.data.list;
          this.total = response.data.total;
          this.listLoading = false;
        });
      },
    }
  }
</script>
<style></style>





